.header-area{
    &.sticky-header {
        position: fixed;
        z-index: 99999;
        left: 0;
        right: 0;
        top: 0;
        animation-name: fadeInDown;
        animation-duration: 1s;
        animation-fill-mode: both;
        @keyframes fadeInDown {
            0% {
            opacity: 0;
            transform: translate3d(0, -100%, 0);
            }

            100% {
                opacity: 1;
                transform: translateZ(0);
            }
        }
    }
    .container-fluid {
        padding-left: 50px;
        padding-right: 50px;
        .nav-bar{
            position: relative;
            display: flex;
            column-gap: 1.5rem;
            justify-content: space-between;
            align-items: center;
            margin-left: auto;
            margin-right: auto;
            .nav-logo{
                font-size: 26px;
                font-weight: bolder;
            }
        }
    }
}